.about{
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}
.about .left {
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;
}
.about .left p {
    margin: 1.2rem 0;
}
.about .right {
    max-width: 850px;
}
.right .img-container {
    display: grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
    margin-bottom: 150px;
}
.right .img{
    max-width: 95%;
    border: 2px solid yellow;
}
.right .top{
    grid-column: 1/span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}
.right .bottom{
    grid-column: 4/-1;
    grid-row: 1;
    
    z-index: 5;
}